.figure-photo {
	display: flex;
	flex-direction: column;
	align-items: center;
	width: 95%;
	margin: 10px auto 100px auto;
	text-align: center;
}

.figure-photo img {
	max-width: 100%;
}

.figure-photo figcaption {
	width: auto;
	font-size: 1.1em;
	color: black;
	margin-top: 10px;
	border: 1px solid black;
	padding: 4px 15px;
}
