.contactForm {
	width: 85%;
	margin: 20px auto;
	text-align: center;
	font-size: 1.5rem;
}
.contactForm label {
	display: block;
	margin: 20px 0px 0px 0px;
}
.contactForm input,
.contactForm textarea,
.contactForm select,
.contactForm button {
	position: relative;
	padding: 7px;
	font-size: 0.7em;
	background-color: rgb(250, 250, 250);
	border: 2px black solid;
	cursor: pointer;
}
.contactForm select {
	width: 100%;
	margin-top: 7px;
}
.contactForm input {
	margin: 10px 25%;
	width: 50%;
}
.contactForm textarea {
	width: 70%;
	min-height: 150px;
	max-width: 100%;
}

.contactForm button.load {
	background-color: rgb(92, 92, 92);
}

.contactForm button.load .icon-spin2 {
	/*spin animation and .icon-spin2 setting are in app.css */
	display: block;
	animation: spin 1.4s linear infinite;
}

.failValidation {
	font-size: 0.55em;
	color: rgb(72, 133, 130);
	position: relative;
}
.failValidation strong {
	position: absolute;
	top: 0%;
	transform: translate(-400%, -25%);
	font-size: 1.6rem;
	color: rgb(119, 24, 24);
}
