.app {
	min-height: 100vh;
	background-color: rgb(255, 255, 255);
	display: flex;
	flex-direction: column;
	flex-wrap: wrap;
	font-size: 85%;
}
nav,
section {
	flex-basis: 100%;
}

@media only screen and (min-width: 900px) {
	.app {
		flex-direction: row;
	}
	nav {
		flex-basis: 40%;
	}
	section {
		flex-basis: 60%;
	}
}

.icon-spin2 {
	display: none;
	position: absolute;
	left: 50%;
	top: 50%;
	color: white;
	transform: translate(-50%, -50%);
}
@keyframes spin {
	from {
		transform: translate(-50%, -50%) rotate(0deg);
	}
	to {
		transform: translate(-50%, -50%) rotate(360deg);
	}
}
